        <h3>登录系统111</h3>
        <div class="partent">
            <div class="login_box">
                <p>欢迎登录我的系统</p>
                <form class="login_form" :model="loginForm">
                    <div class="userName">
                        <span class="text">用户名:</span>
                        <input type="text" v-model="loginForm.userName" placeholder="请输入用户名"/>
                    </div>
                    <div class="passWord">
                        <span class="text">密码:</span>
                        <input type="password" v-model="loginForm.password" placeholder="请输入密码"/>
                    </div>
                </form>
            </div>
            <a href="javascript:;" class="login_btn" @click="login">登录</a>
        </div>
    </div>
</template>
<script>
    export default {
        name:'login',
        data(){
            return {
                loginForm:{
                    userName:'',
                    password:'',
                }
            }
        },
        mounted(){
            console.log(this.$httpServer)
        },
        methods:{
            login(){
                console.log(this.loginForm)
            },
        },
        watch:{

        }
    }
</script>
<style scoped>
    input{
        outline:none;
        width: 70%;
        height: 30px;
        border-radius: 9px;
        border: 1px solid #666;
        padding-left:10px;
    }
    p{
        margin:0;
        height: 30px;
        line-height: 30px;
        padding-left: 6%;
        font-size: 14px;
        border-bottom: 1px dashed #666;
    }
    .login{
        background:url('../assets/loginbg.jpg') no-repeat;
        width:100%;
        height:100%;
        background-size:100% 100%;
        position:absolute;
    }
    .partent{
        position:relative; 
        width:350px;
        height:250px;
        margin:0 auto;
        margin-top:130px;
    }
    .login_box{
        width:350px;
        height:250px;
        margin:0 auto;
        background:#fff;
        opacity:.5;
        margin-top:130px;
        position:relative;
    }
    .login_form{
        width:90%;
        margin-left:5%;
        margin-top:5%;
        position:absolute;
    }
    .userName,.passWord{
        height:50px;
    }
    .text{
        width:50px;
        display:inline-block;
        text-align:right;
        margin-right:15px;
    }
    .login_btn{
        display:block;
        width:91%;
        height:35px;
        background:#2d8cf0;
        color:#fff;
        margin-left:5%;
        border-radius:5px;
        text-align: center;
        line-height:35px;
        position: absolute;
        top: 160px;
        left: 0;
    }
</style>


